import React, { useState, useEffect } from 'react';
import data from './data/Land.json'; // นำเข้าไฟล์ข้อมูล

const LandList = ({ onSelectFeature }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [filteredData, setFilteredData] = useState([]);

    useEffect(() => {
        if (searchTerm.trim() === '') {
            setFilteredData([]); // ถ้าไม่มีการค้นหา ไม่ต้องแสดงผลลัพธ์
            return;
        }

        // ค้นหาข้อมูลโดยใช้ "ชื่อผู้ถือกรรมสิทธิ์"
        const results = data.features.filter((feature) =>
            feature.properties["ชื่อผู้ถือกรรมสิทธิ์"]?.toString().includes(searchTerm)
        );
        setFilteredData(results);
    }, [searchTerm]);

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const handleSelect = (feature) => {
        onSelectFeature(feature); // ส่งค่าฟีเจอร์ที่เลือกไปยัง MapContents
        setSearchTerm(''); // เคลียร์ช่องค้นหาหลังจากเลือก
        setFilteredData([]); // ซ่อนผลลัพธ์หลังจากเลือก
    };

    return (
        <div style={{
            width: '100%', 
            height: '100vh', 
            padding: '10px', 
            background: '#f9f9f9',
            borderRight: '1px solid #ddd',
            boxSizing: 'border-box'
        }}>
            <h3>ค้นหาชื่อเจ้าของ</h3>
            <input
                type="text"
                placeholder="พิมพ์ชื่อเจ้าของ..."
                value={searchTerm}
                onChange={handleSearchChange}
                style={{
                    width: '95%',
                    padding: '8px',
                    marginBottom: '10px',
                    borderRadius: '5px',
                    border: '1px solid #ccc'
                }}
            />
            {filteredData.length > 0 && (
                <ul style={{
                    listStyle: 'none',
                    padding: '5px',
                    maxHeight: '400px',
                    overflowY: 'auto',
                    border: '1px solid #ccc',
                    borderRadius: '5px',
                    background: '#fff'
                }}>
                    {filteredData.map((feature, index) => (
                        <li key={index}
                            onClick={() => handleSelect(feature)}
                            style={{
                                padding: '8px',
                                cursor: 'pointer',
                                borderBottom: '1px solid #ddd'
                            }}>
                            {feature.properties["ชื่อผู้ถือกรรมสิทธิ์"]} 
                            (เลขที่ {feature.properties["เลขที่"]})
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
};

export default LandList;
