import React, { useState, useEffect } from 'react';
import { MapContainer, LayersControl, LayerGroup, TileLayer, GeoJSON, useMap } from 'react-leaflet';
import L from 'leaflet';
import BaseMap from './layer/BaseMap';

//ค้นหาข้อมูล
import LandList from './LandList';

import landData from './data/Land.json';

import Forest from './layer/Forest';
import Province from './layer/Province';
import './map.css';
import icon from 'leaflet/dist/images/marker-icon.png';
import iconShadow from 'leaflet/dist/images/marker-shadow.png';
import Intersec from './layer/Intersec';
import 'leaflet.pattern';

let DefaultIcon = L.icon({
    iconUrl: icon,
    shadowUrl: iconShadow,
    iconSize: [25, 41],
    iconAnchor: [12.5, 20.5]
});
L.Marker.prototype.options.icon = DefaultIcon;

const MapContents = () => {
    const [selectedFeature, setSelectedFeature] = useState(null);

    const MapPopup = ({ feature }) => {
        const map = useMap();

        useEffect(() => {
            // Add Legend
            const legend = L.control({ position: 'bottomleft' });
            legend.onAdd = () => {
                const div = L.DomUtil.create('div', 'legend');
                div.innerHTML = `
                    <div style="display: flex; align-items: center; gap: 5px; background: rgba(255, 255, 255, 0.8); padding: 5px; border-radius: 5px;">
                        <div style="width: 15px; height: 15px; background: green;"></div> พื้นที่ป่าไม้
                        <div style="width: 15px; height: 15px; background: yellow;"></div> พื้นที่ที่เลือกปัจจุบัน
                        <div style="width: 15px; height: 15px; background: orange;"></div> พื้นที่อยู่อาศัยหรือเกษตกรรม
                        <div style="width: 15px; height: 15px; background: red;"></div> พื้นที่ทับซ้อน
                        <div style="width: 15px; height: 15px; background: purple;"></div> มีผลกระทบรุนแรง
                        <div style="width: 15px; height: 15px; background: black; color: white;"></div> อันตราย
                    </div>
                `;
                return div;
            };
            legend.addTo(map);
            return () => map.removeControl(legend);
        }, [map]);

        useEffect(() => {
            if (feature) {
                const { geometry, properties } = feature;
                const coordinates = geometry.coordinates[0][0];
                const latLng = L.latLng(coordinates[1], coordinates[0]);

                // Safely display properties and handle missing data
                const popupContent = Object.entries(properties)
                    .map(([key, value]) => `<strong>${key}:</strong> ${value || 'ไม่ระบุ'}`)
                    .join('<br/>');

                // Open popup and adjust zoom
                L.popup()
                    .setLatLng(latLng)
                    .setContent(popupContent)
                    .openOn(map);

                const currentZoom = map.getZoom();
                if (currentZoom < 20) {
                    map.setView(latLng, 20); // If zoom level is less than 20, zoom in to level 20
                } else {
                    map.setView(latLng); // Keep current zoom level if already >= 20
                }
            }
        }, [feature, map]);

        return null;
    };

    const landStyle = (feature) => {
        const isSelected = selectedFeature && selectedFeature.properties["เลขที่"] === feature.properties["เลขที่"];
        return {
            weight: 1,
            color: isSelected ? 'Black' : 'Black',
            fillColor: isSelected ? 'yellow' : 'Pink',
            fillOpacity: isSelected ? 0.8 : 1
        };
    };

    return (
        <div style={{ display: 'flex', height: '100vh' }}>
            <div style={{ flex: 1 }}>
                <LandList onSelectFeature={setSelectedFeature} />
            </div>

            <div style={{ flex: 3 }}>
                <MapContainer
                    style={{ width: '100%', height: '100%' }}
                    center={[13, 100]}
                    zoom={6}
                    scrollWheelZoom={true}>
                    <TileLayer
                        attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <LayersControl position="topright">
                        <BaseMap />
                        <LayersControl.Overlay name="Land" checked>
                            <LayerGroup>
                                <GeoJSON
                                    data={landData}
                                    style={landStyle}
                                    onEachFeature={(feature, layer) => {
                                        layer.on('click', () => setSelectedFeature(feature));
                                    }}
                                />
                            </LayerGroup>
                        </LayersControl.Overlay>
                        <LayersControl.Overlay name="Intersec" checked>
                            <LayerGroup>
                                <Intersec />
                            </LayerGroup>
                        </LayersControl.Overlay>
                        <LayersControl.Overlay name="Forest" checked>
                            <LayerGroup>
                                <Forest />
                            </LayerGroup>
                        </LayersControl.Overlay>
                        <LayersControl.Overlay name="Province" checked>
                            <LayerGroup>
                                <Province />
                            </LayerGroup>
                        </LayersControl.Overlay>
                    </LayersControl>

                    {selectedFeature && <MapPopup feature={selectedFeature} />}
                </MapContainer>
            </div>
        </div>
    );
};

export default MapContents;
