import React, { useEffect, useRef } from 'react';
import { GeoJSON, useMap } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet.pattern';
import data from '../data/Intersec.json';

const Intersec = () => {
  const handleEachFeature = (feature, layer) => {
    const handleClickFeature = (event) => {
      const layer = event.target;
      const prop = event.target.feature.properties;

      let popupContent = '';
      for (const [key, val] of Object.entries(prop)) {
        popupContent += `<strong>${key}</strong>: ${val} <br />`;
      }

      layer.bindPopup(popupContent).openPopup();
    };

    layer.bindTooltip(feature.properties.ชื่อผู้ถือกรรมสิทธิ์ || "ไม่มีข้อมูล", {
      direction: 'right',
    });

    layer.on({
      'click': handleClickFeature
    });
  };

  const map = useMap();
  const patternRef = useRef(null);

  useEffect(() => {
    if (!patternRef.current) {
      patternRef.current = new L.StripePattern({
        weight: 2,
        spaceWeight: 1,
        color: 'red',
        opacity: 1
      });

      map.whenReady(() => {
        patternRef.current.addTo(map);
      });
    }
  }, [map]);

  const geoJsonStyle = () => ({
    weight: 1,
    color: 'Red',
    fillPattern: patternRef.current
  });

  return <GeoJSON data={data} style={geoJsonStyle} onEachFeature={handleEachFeature} />;
};

export default Intersec;
